import React from 'react';
import {Link} from "react-router-dom";

function Sidebar() {
    return (
        <div id="layoutSidenav_nav">
            {/* <!-- Sidebar --> */}
            <nav className="sb-sidenav accordion sb-sidenav-dark" id="sidenavAccordion">
                <div className="sb-sidenav-menu">
                    <div className="nav">
                        <div className="sb-sidenav-menu-heading">Core</div>
                        <a className="nav-link small" href="#">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-tachometer-alt"></i>
                                <span className="text-light font-weight-bold mx-2">Dashboard</span>
                            </div>
                        </a>
                        
                        <div className="sb-sidenav-menu-heading">Interface</div>
                        <a className="nav-link collapsed" href="#" data-toggle="collapse" data-target="#collapseLayouts" aria-expanded="false" aria-controls="collapseLayouts">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-columns"></i>
                                <span className="text-light font-weight-bold mx-2">Layouts</span>
                            </div>
                            <div className="sb-sidenav-collapse-arrow"><i className="fas fa-angle-down"></i></div>
                        </a>
                        <div className="collapse" id="collapseLayouts" aria-labelledby="headingOne" data-parent="#sidenavAccordion">
                            <nav className="sb-sidenav-menu-nested nav">
                                <a className="nav-link small" href="#">Static Navigation</a>
                                <a className="nav-link small" href="#">Light Sidenav</a>
                            </nav>
                        </div>
                        <a className="nav-link collapsed" href="#" data-toggle="collapse" data-target="#collapsePages" aria-expanded="false" aria-controls="collapsePages">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-book-open"></i>
                                <span className="text-light font-weight-bold mx-2">Pages</span>
                            </div>
                            <div className="sb-sidenav-collapse-arrow"><i className="fas fa-angle-down"></i></div>
                        </a>
                        <div className="collapse" id="collapsePages" aria-labelledby="headingTwo" data-parent="#sidenavAccordion">
                            <nav className="sb-sidenav-menu-nested nav accordion" id="sidenavAccordionPages">
                                <a className="nav-link small collapsed" href="#" data-toggle="collapse" data-target="#pagesCollapseAuth" aria-expanded="false" aria-controls="pagesCollapseAuth">
                                    Authentication
                                    <div className="sb-sidenav-collapse-arrow"><i className="fas fa-angle-down"></i></div>
                                </a>
                                <div className="collapse" id="pagesCollapseAuth" aria-labelledby="headingOne" data-parent="#sidenavAccordionPages">
                                    <nav className="sb-sidenav-menu-nested nav">
                                        <a className="nav-link small" href="#">Login</a>
                                        <a className="nav-link small" href="#">Register</a>
                                        <a className="nav-link small" href="#">Forgot Password</a>
                                    </nav>
                                </div>
                                <a className="nav-link collapsed small" href="#" data-toggle="collapse" data-target="#pagesCollapseError" aria-expanded="false" aria-controls="pagesCollapseError">Error
                                    <div className="sb-sidenav-collapse-arrow"><i className="fas fa-angle-down"></i></div>
                                </a>
                                <div className="collapse" id="pagesCollapseError" aria-labelledby="headingOne" data-parent="#sidenavAccordionPages">
                                    <nav className="sb-sidenav-menu-nested nav">
                                        <a className="nav-link small" href="#">401 Page</a>
                                        <a className="nav-link small" href="#">404 Page</a>
                                        <a className="nav-link small" href="#">500 Page</a>
                                    </nav>
                                </div>
                            </nav>
                        </div>
                        <div className="sb-sidenav-menu-heading">Addons</div>
                        <a className="nav-link small" href="#">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-chart-area"></i>
                                <span className="text-light font-weight-bold mx-2">Charts</span>
                            </div>
                        </a>
                        <a className="nav-link small" href="#">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-table"></i>
                                <span className="text-light font-weight-bold mx-2">Tables</span>
                            </div>
                        </a>
                    </div>
                </div>
            </nav>
            {/* <!-- End of Sidebar --> */}
        </div>
    )
};

export default Sidebar;