/* Main.Js */

import React from 'react';
import { Switch } from "react-router-dom";
import { Public, Private } from './assets/js/utils/AccessRoute';

import 'bootstrap';
import 'startbootstrap-sb-admin-2/css/sb-admin-2.min.css';
import 'startbootstrap-sb-admin-2/vendor/fontawesome-free/css/all.min.css';

import './assets/css/custom.css';
import './assets/css/navigation.css';

import Topbar from './components/bg-topbar';
import Sidebar from './components/bg-sidebar';
import Footer from './components/bg-footer';

import Dashboard from './views/bg-dashboard';
import Error from './views/bg-error';
 
function Main (props) {
    return (
        <div id="content-wrapper" className="d-flex flex-column">
            <div className="sb-nav-fixed" id="content">
                
                <Topbar {...props} />
                <div id="layoutSidenav">
                    <Sidebar/>
                    <div id="layoutSidenav_content">
                        <Switch>
                            <Public exact path="/" component={Dashboard} />
                            <Public exact path="/help" component={Error} />
                        </Switch>
                        <Footer />
                    </div>
                </div>

            </div> 
        </div>
    );
};

export default Main;
