import React from 'react';
import Main from './bg-Main';
import Auth from './bg-Auth';

import { Switch } from "react-router-dom";
import { Public, Private } from './assets/js/utils/AccessRoute';

function App() {
    return (
        <div>
            <Switch>
                <Private exact path="/auth" component={Auth} />
                <Public exact path="/" component={Main} />
            </Switch>
      </div>
    );
};

export default App;
