import React from 'react';
import ApexChartGauge from '../components/charts/bg-apexChart-gauge';
import ApexChartLine from '../components/charts/bg-apexChart-line';

import {error} from './bg-error';
import Card, { CardBody } from '../components/bg-card';

function Dashboard() {
    return (
        <div className="container-fluid mt-2 pt-2">

            <div className="d-flex align-items-center justify-content-between mb-3">
                <div className="d-none d-sm-inline h3 text-dark my-auto">Dashboard</div>
                <div className="d-none d-sm-inline">
                    <button className="btn btn-sm btn-warning btn-icon-split font-weight-bold" type="button">
                        <span className="icon text-white"><i className="fas fa-fw fa-sm fa-download"></i></span>
                        <span className="text px-4">Generate Report</span>
                    </button>
                </div>
            </div>

<           div className="row">
                <div className="col-md-12 col-xl-4 mb-2">

                    <div class="card shadow rounded-0 mb-3">
                        <small class="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                            Biogas Reactor
                        </small>
                    
                        <div class="card-body">
                            <div id="db-carousel" className="carousel slide" data-ride="carousel">
                                <div className="carousel-inner" data-toggle="modal" data-target="#data-modal">
                                    <div className="carousel-item active" data-interval="15000">
                                        <CardBody header="Temperature A" value="12.76"/>
                                    </div>
                                    <div className="carousel-item" data-interval="15000">
                                        <CardBody header="Temperature B" value="15.77"/>
                                    </div>
                                </div>
                            </div>

                            {/* <!-- Modal control --> */}
                            <a className="carousel-control-prev" href="#db-carousel" role="button" data-slide="prev"></a>
                            <a className="carousel-control-next" href="#db-carousel" role="button" data-slide="next"></a>

                            <div class="d-block dropdown-divider my-auto"></div>
                            <ApexChartGauge chartLabel="Pressure (kPa)" chartHeight={320}/>
                        </div>
                    </div>
                </div>
                    
                <div className="col-md-12 col-xl-4 mb-2">                    
                    <div class="card shadow rounded-0 mb-3">
                        <small class="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                            Pressurized Storage
                        </small>
                    
                        <div class="card-body">
                            <CardBody header="Temperature" value="23.65"/>
                            <div class="d-block dropdown-divider my-auto"></div>
                            <ApexChartGauge chartLabel="Pressure (kPa)" chartHeight={320}/>
                        </div>
                    </div>
                </div>

                <div className="col-md-12 col-xl-4 mb-2">
                    <div class="card shadow rounded-0 mb-3">
                        <small class="card-header bg-asphalt text-white text-center font-weight-bold rounded-0 py-2">
                            Normal Storage
                        </small>
                    
                        <div class="card-body">
                            <CardBody header="Temperature" value="29.98"/>
                            <div class="d-block dropdown-divider my-auto"></div>
                            <ApexChartGauge chartLabel="Pressure (kPa)" chartHeight={320}/>
                        </div>
                    </div>
                </div>
            </div>
                
            {/* <!-- 404 Error Text --> */}
            <div className="d-block d-sm-none">{error ('Mobile view is not supported yet :(')}</div>
        </div>
    );
};

export default Dashboard;